import React  from "react";
import { createRoot } from 'react-dom/client';
import { comments , currentUser } from "./interactive-comments-section-main/data.json";
import './App.css';

localStorage.setItem('comments',JSON.stringify(comments))
var comentarios = JSON.parse(localStorage.getItem('comments'))
localStorage.setItem('user',JSON.stringify(currentUser))
var usuario = JSON.parse(localStorage.getItem('user'))


function replace(x){
  let sp1 = document.createElement('div');
  let sp2 = document.getElementById(x.id);
  let divPai = sp2.parentNode;
  divPai.insertBefore(sp1, sp2.nextSibling);
  let a = (
    <div className="comentar">
      <img width='50' height='50' src={usuario.image.png} alt="imagem do usuario" />
      <textarea defaultValue={'@' + document.querySelector( `.card[id="${x.id}"] > div.stwo > div.stwoi > div > span:nth-child(2)`).outerText + ', '} rows={4} placeholder={`Add a comment...`} />
      <button >Send</button>
    </div>
  )
  const container = document.getElementById(x.id).nextSibling;
  const root = createRoot(container);
  root.render(a)
}

function Card(props) {
  let x = props.x;
  let b = props.b;
  props.b != null ? x = b : x = props.x;
  const [score ,setScore] = React.useState(x.score)

  return(
    <div className="card" id={x.id}>
      <div className="sone">
        <button onClick={()=>{
          setScore(score+1)
        }} type="button">
          <svg width="11" height="11" xmlns="http://www.w3.org/2000/svg"><path d="M6.33 10.896c.137 0 .255-.05.354-.149.1-.1.149-.217.149-.354V7.004h3.315c.136 0 .254-.05.354-.149.099-.1.148-.217.148-.354V5.272a.483.483 0 0 0-.148-.354.483.483 0 0 0-.354-.149H6.833V1.4a.483.483 0 0 0-.149-.354.483.483 0 0 0-.354-.149H4.915a.483.483 0 0 0-.354.149c-.1.1-.149.217-.149.354v3.37H1.08a.483.483 0 0 0-.354.15c-.1.099-.149.217-.149.353v1.23c0 .136.05.254.149.353.1.1.217.149.354.149h3.333v3.39c0 .136.05.254.15.353.098.1.216.149.353.149H6.33Z" fill="#C5C6EF"/></svg>
        </button>
        <span>{score}</span>
        <button onClick={()=>{
          setScore(score-1)
        }} type="button">
          <svg width="11" height="3" xmlns="http://www.w3.org/2000/svg"><path d="M9.256 2.66c.204 0 .38-.056.53-.167.148-.11.222-.243.222-.396V.722c0-.152-.074-.284-.223-.395a.859.859 0 0 0-.53-.167H.76a.859.859 0 0 0-.53.167C.083.437.009.57.009.722v1.375c0 .153.074.285.223.396a.859.859 0 0 0 .53.167h8.495Z" fill="#C5C6EF"/></svg>
        </button>
      </div>
    <div className="stwo">
      <div className="stwoi">
        <div className="stwoii">
          <img src={x.user.image.png} alt="" width="40em" />
          <span Style="margin: 0em 1em 0em 1em;">{x.user.username}</span>
          <span Style="margin: 0em 1em 0em 1em; width: 15vh;">{x.createdAt}</span>
        </div>
          <button onClick={()=>{replace(x)}} className="Breply">
             <svg width="14" height="13" xmlns="http://www.w3.org/2000/svg">
              <path d="M.227 4.316 5.04.16a.657.657 0 0 1 1.085.497v2.189c4.392.05 7.875.93 7.875 5.093 0 1.68-1.082 3.344-2.279 4.214-.373.272-.905-.07-.767-.51 1.24-3.964-.588-5.017-4.829-5.078v2.404c0 .566-.664.86-1.085.496L.227 5.31a.657.657 0 0 1 0-.993Z" fill="#5357B6"/>
              </svg>
              <span>Reply</span>
          </button>
        </div>
        <div className="sectionText">
          <p>{x.content}</p>
        </div>
      </div>
      <div className="cell">
        <div className="sone">
          <button onClick={()=>{
            setScore(score+1)
          }} type="button">
            <svg width="11" height="11" xmlns="http://www.w3.org/2000/svg"><path d="M6.33 10.896c.137 0 .255-.05.354-.149.1-.1.149-.217.149-.354V7.004h3.315c.136 0 .254-.05.354-.149.099-.1.148-.217.148-.354V5.272a.483.483 0 0 0-.148-.354.483.483 0 0 0-.354-.149H6.833V1.4a.483.483 0 0 0-.149-.354.483.483 0 0 0-.354-.149H4.915a.483.483 0 0 0-.354.149c-.1.1-.149.217-.149.354v3.37H1.08a.483.483 0 0 0-.354.15c-.1.099-.149.217-.149.353v1.23c0 .136.05.254.149.353.1.1.217.149.354.149h3.333v3.39c0 .136.05.254.15.353.098.1.216.149.353.149H6.33Z" fill="#C5C6EF"/></svg>
          </button>
          <span>{score}</span>
          <button onClick={()=>{
            setScore(score-1)
          }} type="button">
            <svg width="11" height="3" xmlns="http://www.w3.org/2000/svg"><path d="M9.256 2.66c.204 0 .38-.056.53-.167.148-.11.222-.243.222-.396V.722c0-.152-.074-.284-.223-.395a.859.859 0 0 0-.53-.167H.76a.859.859 0 0 0-.53.167C.083.437.009.57.009.722v1.375c0 .153.074.285.223.396a.859.859 0 0 0 .53.167h8.495Z" fill="#C5C6EF"/></svg>
          </button>
        </div>
        <button onClick={()=>{replace(x)}} className="Breply">
             <svg width="14" height="13" xmlns="http://www.w3.org/2000/svg">
              <path d="M.227 4.316 5.04.16a.657.657 0 0 1 1.085.497v2.189c4.392.05 7.875.93 7.875 5.093 0 1.68-1.082 3.344-2.279 4.214-.373.272-.905-.07-.767-.51 1.24-3.964-.588-5.017-4.829-5.078v2.404c0 .566-.664.86-1.085.496L.227 5.31a.657.657 0 0 1 0-.993Z" fill="#5357B6"/>
            </svg>
            <span>Reply</span>
        </button>
      </div>
    </div>
  )

}

function CardWithReplies(props){
  let card = props.x
  return(
    <>
      <Card x={card}/>
      <div className="reply">
        <div>
          {card.replies.map((x)=>{
            return <Card b={x} x={card}/>
          })}
        </div>
      </div>
    </>
  )
}


function App() {
  let [coment , setComentarios] = React.useState(comentarios)
  let [content,setContent] = React.useState('')
  function send(){
    try{
      const modalJson =     
      {
          "id": comentarios.length +1,
          "content": content.target.value,
          "createdAt": "now",
          "score": 0,
          "user": {
            "image": { 
              "png": "./images/avatars/image-juliusomo.png",
              "webp": "./images/avatars/image-juliusomo.webp"
            },
            "username": "juliusomo"
          },
          "replies": []
      }
      comentarios = comentarios.concat(modalJson)
      localStorage.setItem('comments',comentarios)
      setComentarios(comentarios)
      setContent('')
      document.querySelector('#root > main > div.comentar > textarea').value = ''
    }catch(error){
      console.error()
    }
  }
  return(
    <main>
    {coment.map((comentario)=>{
        if(comentario.replies.length === 0){
          return <Card x={comentario} y={setComentarios}/>
        }else{
          return <CardWithReplies x={comentario}/>
        }
    })}
    <div className="comentar">
      <img width='50' height='50' src={usuario.image.png} alt="imagem do usuario" />
      <textarea rows={4} placeholder="Add a comment..." onChange={setContent}/>
      <button onClick={send} >
          Send
      </button>
    </div>
  </main>
  );
}
export default App;